.portfolio {
    padding: 60px 20px;
    background-color: #333;
    color: white;
  }
  
  .portfolio-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
  }
  
  .portfolio-card {
    background-color: #444;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
  }
  