.navbar {
    display: flex;
    justify-content: center;
    padding: 10px;
    background-color: #1f1f1f;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
  }
  
  .navbar ul {
    list-style: none;
    display: flex;
    gap: 20px;
  }
  
  .navbar ul li {
    display: inline;
  }
  
  .navbar ul li a {
    color: white;
    text-decoration: none;
    padding: 10px 15px;
    transition: background-color 0.3s;
  }
  
  .navbar ul li a:hover {
    background-color: #333;
    border-radius: 5px;
  }
  